<template>
  <div id="searchdiv">
    <el-row>
      <el-col :span="22">
        <el-input id="link" placeholder="Video link" v-model="link"></el-input>
      </el-col>
      <el-col :span="2">
        <el-button id="playVid" icon="el-icon-right" @click="passCode"></el-button>
      </el-col>
    </el-row>
  </div>
</template>

<script>
export default {
  name: "SearchBar",
  data() {
    return {
      link: "",
      videoCode: "",
    };
  },

  methods: {
    getCode() {
      this.videoCode = this.link.slice(32);
      this.link = "";
      return this.videoCode;
    },
    passCode() {
      this.$emit("clicked", this.getCode());
    },
  },
};
</script>

<style>
#searchdiv {
  display: inline-block;
  width: 50%;
}

#playVid {
  background-color: #b4f8c8;
  border: 0;
  border-radius: 20px;
}

#playVid:hover {
  background-color: #b4f098;
  border: 0;
  border-radius: 20px;
}

#playVid:active {
  transform: scale(0.7);
  background-color: #b4f098;
  border: 0;
  border-radius: 20px;
}

#link {
  width: 100%;
  border: 0;
  text-align: center;
  border-radius: 20px;
  background-color: #fbe7c6;
}
</style>
